.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
}
.inner-popup {
  padding: 5px 10px;
  height: 150px;
  border-radius: 12px;
  background: white;
  z-index: 9999999999;
}
